body {
  background: #030017;
  color: #ffffff;
  padding: 0;
  margin: 0;
  font-family: "Poppins", "san-serif";
}

ul {
  padding: 0;
  margin: 0;
}

li {
  padding: 0;
  margin: 0;
  list-style: none;
}

a {
  text-decoration: none;
  color: #ffffff;
}

header {
  margin-top: 24px;
}
header .container .row .col-md-4 {
  display: flex;
  align-items: center;
}
header .container .row .col-md-4 ul {
  display: flex;
  justify-content: center;
  align-items: center;
}
header .container .row .col-md-4 ul li {
  padding: 0 1rem;
}
header .container .row .col-md-4:last-child {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

main div .container h2 {
  display: block;
  text-align: center;
}
main .section-opening .container h1 {
  font-weight: 600;
  font-size: 86px;
  line-height: 135%;
  text-align: center;
}
main .section-how-work .container .row {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap;
}
main .section-how-work .container .row .icon-item {
  padding: 5px;
  width: 18%;
  margin: 10px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
}
main .section-how-work .container .row .icon-item h2 {
  text-align: center;
}

footer .container .row {
  padding: 56px 0;
}
footer .container .row > div {
  display: flex;
  align-items: center;
}
footer .container .row .col-md-5 ul {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
footer .container .row .col-md-5 ul li {
  padding: 0 1rem;
}
footer .container .row .col-md-5 ul li:first-child {
  padding: 0 0;
}
footer .container .row .col-md-2:nth-child(2) {
  justify-content: center;
}
footer .container .row .col-md-5:last-child {
  justify-content: flex-end;
}
footer .container .row .col-md-5:last-child ul li a span {
  width: 24px;
  height: 24px;
  display: inline-block;
  margin-right: 10px;
}
footer .container .row .col-md-5:last-child ul li a #icon-dribbble {
  background-image: url("https://hyperfantasy.co/assets/styles/images/icon-social-media-dribbble.png");
}
footer .container .row .col-md-5:last-child ul li a #icon-instagram {
  background-image: url("https://hyperfantasy.co/assets/styles/images/icon-social-media-instagram.png");
}
footer .container .row .col-md-5:last-child ul li:first-child {
  font-size: 12px;
  margin-right: 24px;
}